@font-face {
  font-family: "Lato";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/lato/lato-regular.woff2") format("woff2"), url("../fonts/lato/lato-regular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswald/oswald-regular.woff2") format("woff2"), url("../fonts/oswald/oswald-regular.woff") format("woff");
}
body {
  margin: 0;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-style: normal;
  height: 100%;
  color: #444444;
  background-color: #ffffff;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.wrapper {
  width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .wrapper {
    width: 768px;
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    width: 1440px;
    padding: 0 110px;
  }
}

.title {
  margin: 0;
  font-family: "Oswald", "Verdana", sans-serif;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: 0.1px;
}

p {
  margin: 0;
}

.button {
  font-family: "Oswald", "Verdana", sans-serif;
  font-size: 14px;
  line-height: 14px;
  padding: 10px;
  background-color: #68b738;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  border: none;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active .button__desc {
  opacity: 30%;
}

.list-none {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  position: relative;
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 1440px) {
