.header-navigation {
  width: auto;
  min-height: 38px;
  z-index: 2;
}

.header-navigation__toggle {
  position: absolute;
  top: 24px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 24px;
  height: 24px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .header-navigation__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .header-navigation--opened .header-navigation__list {
    display: block;
  }
}

@media (max-width: 767px) {
  .header-navigation--closed .header-navigation__list {
    display: none;
  }
}

.header-navigation__toggle .header-navigation--nojs {
  display: none;
}

.header-navigation--closed .header-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
@media (min-width: 768px) {
  .header-navigation--closed .header-navigation__toggle::before {
    display: none;
  }
}

@media (max-width: 767px) {
  .header-navigation--nojs .header-navigation__toggle {
    display: none;
  }
}
.header-navigation--opened .header-navigation__toggle::before, .header-navigation--opened .header-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 5px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.header-navigation--opened .header-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.header-navigation--opened .header-navigation__toggle::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .header-navigation__list {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .header-navigation__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 auto;
    gap: 33px;
  }
}

@media (max-width: 767px) {
  .header-navigation--closed .header-navigation__list {
    display: none;
  }
}

@media (min-width: 768px) {
  .header-navigation__item--active {
    position: relative;
  }
  .header-navigation__item--active::after {
    content: "";
    position: absolute;
    top: 30px;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background: #68b738;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .header-navigation__item--active::after {
    top: 35px;
  }
}

@media (min-width: 1440px) {
  .header--index .header-navigation__item::after {
    background: #ffffff;
  }
}

.header-navigation__list a {
  position: relative;
  display: block;
  font-family: "Oswald", "Verdana", sans-serif;
  font-size: 20px;
  line-height: 20px;
  padding: 23px;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
}
.header-navigation__list a:hover {
  opacity: 0.6;
}
.header-navigation__list a:active {
  opacity: 0.8;
}
@media (min-width: 768px) {
  .header-navigation__list a {
    font-size: 18px;
    line-height: 24px;
    padding: 0;
    border: none;
  }
}
@media (min-width: 1440px) {
  .header-navigation__list a {
    font-size: 20px;
    line-height: 30px;
    border: none;
  }
}

@media (min-width: 1440px) {
  .header-navigation__list--index a {
    color: #ffffff;
  }
}

@media (max-width: 767px) {
  .header-navigation--nojs .header-navigation__list {
    display: block;
    position: static;
    min-height: 0;
  }
}

